body {
  background-color: #262938;
  font-family: 'niveau-grotesk', sans-serif; }

.main-area {
  background-color: #FFF; }

svg {
  display: inline-block;
  width: 16px;
  height: 16px;
  color: currentColor; }

/** Elements **/
header {
  box-shadow: 0 1px 2px rgba(92, 97, 99, 0.25);
  position: relative; }

/* Header Top Bar */
header .above {
  background-color: #f5f6f7;
  color: #FFFFFF;
  padding: 0.375rem; }

@supports (display: flex) {
  header .contain {
    display: flex; } }

/* Right aligned items */
@supports (display: flex) {
  .header-above-right {
    margin-left: auto; } }

/* Little Top Nav */
header .above nav {
  float: left; }
  @supports (display: flex) {
    header .above nav {
      display: flex; } }

header .above nav li {
  float: left; }

header .above nav li a {
  display: block;
  color: #8e98a6;
  text-decoration: none;
  font-size: 0.875rem;
  padding: 0.375rem;
  line-height: 1rem; }

header .above nav li a:hover,
header .above nav li.current a {
  color: #6863ce; }

/* Social */
header .above .social {
  float: left;
  margin-right: 1rem; }
  @supports (display: flex) {
    header .above .social {
      margin-left: auto; } }

header .above .social li {
  float: left; }

header .above .social li a {
  display: block;
  padding: 0.375rem; }

header .above .social li a svg {
  fill: #8e98a6;
  display: block;
  width: 16px;
  height: 16px; }

header .above .social li a:hover svg {
  fill: #6863ce; }

/* Search */
header .above .search-field {
  float: right;
  position: relative; }
  @supports (display: flex) {
    header .above .search-field {
      margin-left: auto; } }

header .above .search-field input[type="search"] {
  border-radius: 20px;
  background: #FFFFFF;
  border: none;
  font-size: 0.875rem;
  line-height: 1.75rem;
  padding: 0 3rem 0 0.75rem;
  width: 120px; }

header .above .search-field button {
  position: absolute;
  top: 0.375rem;
  right: 0.375rem;
  background: none;
  border: none;
  color: blue;
  padding: 0;
  margin: 0; }

/* Below */
header .below {
  padding: 0;
  background-color: #FFF;
  position: relative; }
  header .below:after {
    content: '';
    display: table;
    clear: both; }

header .below .right-items {
  text-align: right;
  float: right; }

header .below .header-ctoa {
  margin: 40px 0 0;
  font-size: 0; }

header .below .header-ctoa .phone:last-child {
  border-left: none; }

header .below .phone {
  display: inline-block;
  color: #6863ce;
  font-size: 1.125rem;
  line-height: 20px;
  font-weight: bold;
  padding: 0 0 0 30px;
  margin-left: 10px;
  text-decoration: none;
  position: relative; }

header .below .phone svg {
  fill: #6863ce;
  display: block;
  position: absolute;
  top: 50%;
  margin-top: -8px;
  left: 8px; }

/* Logo */
header a.logo {
  display: block;
  text-decoration: none;
  float: left;
  position: relative;
  margin: 40px 0; }

header a.logo .tagline {
  color: #435158;
  position: absolute;
  top: 100%;
  margin-top: 2px;
  right: 20px;
  font-size: 14px; }

/* Main Nav */
header .below nav {
  text-align: right;
  font-size: 0; }
  @supports (display: flex) {
    header .below nav {
      display: flex;
      align-items: center;
      justify-content: center; } }

header .below nav ul {
  position: relative;
  margin: 0; }

header .below nav li {
  display: inline-block;
  font-size: 1rem;
  margin: 15px 0 15px 30px;
  list-style: none;
  position: relative; }

header .below nav li a {
  color: #162a48;
  text-align: center;
  display: block;
  text-decoration: none;
  font-size: 1.25rem;
  line-height: 30px;
  white-space: nowrap;
  position: relative;
  font-weight: bold; }

header .below nav li a:hover,
header .below nav li.current a {
  color: #6863ce; }

/* Dropdown */
header .below nav li a .dropdown-connector {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  height: 20px;
  transition: 0.2s margin-top ease 0.2s, 0.2s opacity ease 0.2s;
  margin-top: 10px;
  visibility: hidden;
  opacity: 0; }
  header .below nav li a .dropdown-connector:after {
    bottom: 0;
    left: 50%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-color: rgba(255, 255, 255, 0);
    border-bottom-color: #FFF;
    border-width: 12px;
    margin-left: -12px; }

header .below nav li:hover a .dropdown-connector {
  opacity: 1;
  visibility: visible;
  margin-top: 0;
  transition: 0.2s margin-top ease 0.2s, 0.2s opacity ease 0.2s;
  z-index: 3; }

header .below nav li .dropdown {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 100%;
  margin-top: 30px;
  left: 0;
  width: 300px;
  border-radius: 20px;
  background-color: #FFF;
  transition: 0.2s margin-top ease, 0.2s opacity ease;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1); }

header .below nav li:hover .dropdown {
  display: block;
  visibility: visible;
  opacity: 1;
  margin-top: 20px;
  transition: 0.2s margin-top ease 0.2s, 0.2s opacity ease 0.2s; }

header .below nav li .dropdown .bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  border-radius: 0 8px 8px 0;
  width: 100%;
  background-size: cover;
  background-position: center; }

header .below nav li .dropdown .bg:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  background: linear-gradient(to left, rgba(255, 255, 255, 0.75) 0%, white 100%); }

header .below nav li ul {
  padding: 1.5rem;
  text-align: left;
  margin: 0; }

header .below nav li ul li {
  display: block;
  margin: 0; }

header .below nav li ul li:not(:last-child) {
  margin: 0 0 0.25rem; }

header .below nav li ul li a {
  display: block;
  text-align: left;
  font-size: 1rem;
  color: #000;
  white-space: normal; }

header .below nav li ul li a:focus,
header .below nav li ul li a:hover {
  color: #000; }

/* Third Level */
header .below nav li ul li ul {
  padding: 0;
  position: static; }

header .below nav li ul li ul li {
  margin: 0; }

header .below nav li ul li ul li a {
  font-size: 1rem; }

