  fill: #00a0ad; }

.home-banner {
  position: relative;
  overflow: hidden; }
  .home-banner.loading .loader {
    display: block;
    opacity: 1;
    visibility: visible; }

.home-banner .loader {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -50px;
  margin-left: -50px;
  z-index: 2; }

/* Home Slider */
.slide {
  padding-top: 30%;
  position: relative;
  background-color: #EFEFEF; }

.home-banner .slide .image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }
  @supports (display: flex) {
    .home-banner .slide .image {
      display: flex;
      flex-direction: column;
      justify-content: center; } }

.slide .image:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 60%;
  background: linear-gradient(90deg, rgba(22, 30, 32, 0.65) 0%, transparent 100%); }

.slide .overlay {
  position: relative;
  z-index: 2;
  width: 100%;
  animation: show 1s ease 1; }

@keyframes show {
  0% {
    opacity: 0;
    transform: translateY(100px); }
  100% {
    opacity: 1;
    transform: translateY(0px); } }

.slide .caption {
  padding: 3rem 0;
  color: #FFFFFF;
  font-size: 1rem;
  line-height: 1;
  white-space: normal; }

.slide .caption h2 {
  font-size: 3rem; }

.slide .caption p {
  font-size: 1.125rem;
  line-height: 1.5;
  margin-top: 0.5rem; }

.slide .caption a.go {
  display: inline-block;
  text-align: center;
  padding: 16px 20px;
  font-size: 1.5rem;
  font-weight: bold;
  color: #FFFFFF;
  text-decoration: none;
  border-radius: 5px;
  margin-top: 1rem;
  background-color: #00a0ad; }

/**
 * (optional) define here the style definitions which should be applied on the slider container
 * e.g. width including further controls like arrows etc.
 */
.js_slider {
  position: relative; }
  .js_slider .frame {
    position: relative;
    font-size: 0;
    line-height: 0;
    overflow: hidden;
    white-space: nowrap; }
  .js_slider .slides {
    display: inline-block;
    width: 100%; }
  .js_slider li {
    position: relative;
    display: inline-block;
    width: 100%; }
  .js_slider .prev,
  .js_slider .next {
    position: absolute;
    top: 50%;
    margin-top: -25px;
    display: block;
    cursor: pointer;
    width: 50px;
    height: 50px;
    background-color: rgba(0, 0, 0, 0.5);
    transition: 0.5s all ease; }
  .js_slider .prev:hover,
  .js_slider .next:hover {
    background-color: #00a0ad; }
  .js_slider .prev {
    left: 0;
    border-radius: 0 10px 10px 0; }
  .js_slider .next {
    right: 0;
    border-radius: 10px 0 0 10px; }
  .js_slider .next svg,
  .js_slider .prev svg {
    width: 25px;
    height: 50px;
    fill: #FFFFFF;
    display: block;
    margin: 0 auto; }

@media screen and (max-width: 1360px) {
  .slide {
    padding-top: 50%; } }

@media screen and (max-width: 800px) {
  .slide {
    padding-top: 0%; }
  .home-banner .slide .image {
